import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import DescTab from "./DescTab";
import InfoTab from "./InfoTab";
import ReviewTab from "./ReviewTab";

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: "transparent",
    },
    labels: {
        color:"#6C7592",
        "&:hover": {
            transitionDuration: "50ms",
            color: "#1266F1",
        }
    },
    indicator: {
        backgroundColor:"#1266F1",
    }
}));

export default function SimpleTabs() {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Tabs classes={{indicator: classes.indicator}}
                      value={value}
                      onChange={handleChange}
                      aria-label="simple tabs example"
                      style={{backgroundColor:"white"}}
                      variant="fullWidth"
                      centered>
                    <Tab label="Description" className={classes.labels} {...a11yProps(0)} />
                    <Tab label="Information" className={classes.labels}{...a11yProps(1)} />
                    <Tab label="Review" className={classes.labels}{...a11yProps(2)} />
                </Tabs>
            </AppBar>
            <TabPanel value={value} index={0}>
                <DescTab/>
            </TabPanel>
            <TabPanel value={value} index={1}>
                <InfoTab/>
            </TabPanel>
            <TabPanel value={value} index={2}>
                <ReviewTab/>
            </TabPanel>
        </div>
    );
}
